.forceGraph path.link {
  fill: none;
  stroke: #bbb;
  cursor: pointer;
}
.forceGraph circle {
  fill: #ccc;
  stroke: #fff;
  stroke-width: 1.5px;
  cursor: pointer;
}
.forceGraph text {
  font: 12px sans-serif;
  pointer-events: none;
  z-index: 99;
}
.forceGraph .hidden {
  display: none;
}



