.histogram {
  font-family: 'Segoe UI', 'wf_segoe-ui_normal', helvetica, arial, sans-serif;
}
.histogram .axis path,
.histogram .legends path,
.histogram .axis line,
.histogram .legends line {
  fill: none;
  stroke: #777777;
  shape-rendering: crispEdges;
}
.histogram .axis text,
.histogram .legends text {
  fill: #777777;
  font-size: 11px;
}
.histogram .legends text {
  font-size: 12px;
  fill: #000;
  text-anchor: middle;
}



