.Timeline .label,
.Timeline .mainArea .label,
.Timeline .upperTextCell,
.Timeline .lowerTextCell {
  text-anchor: middle;
}
.Timeline .cellRect {
  stroke: #333444;
}
.Timeline .selectionCursor {
  fill: gray;
}
.Timeline .selectionRangeContainer {
  float: none;
  position: absolute;
  text-anchor: start;
}
.Timeline .timelineSlicer {
  position: absolute;
  top: 20px;
  right: 17px;
  fill: #000000;
}
.Timeline .timelineSlicer .periodSlicerRect {
  stroke: #aaa;
  stroke-width: 2px;
  fill: transparent;
}
.Timeline .timelineSlicer .periodSlicerGranularities {
  font-family: sans-serif;
  font-size: 10px;
}
.Timeline .timelineSlicer .periodSlicerSelection {
  font-family: sans-serif;
  font-size: 10px;
  text-anchor: middle;
}
.Timeline .timelineSlicer .periodSlicerSelectionRect {
  fill: transparent;
}



