.lineDotChart {
  font-family: 'Segoe UI', 'wf_segoe-ui_normal', helvetica, arial, sans-serif;
}
.lineDotChart .axis path,
.lineDotChart .legends path,
.lineDotChart .axis line,
.lineDotChart .legends line {
  fill: none;
  stroke: black;
  shape-rendering: crispEdges;
}
.lineDotChart .axis text,
.lineDotChart .legends text {
  fill: black;
  font-size: 14px;
}
.lineDotChart .legends text {
  font-size: 16px;
  fill: #000;
  text-anchor: middle;
}
.lineDotChart .line {
  fill: none;
  stroke-width: 2px;
}
.lineDotChart .point {
  stroke: white;
  stroke-opacity: .7;
  stroke-width: .5;
  pointer-events: all;
  transition: opacity 0.2s ease-out;
}
.lineDotChart .text {
  fill: black;
  font-size: 32px;
  text-align: right;
}
.lineDotChart.filtered .point {
  opacity: .2;
}
.lineDotChart.filtered .point_selected {
  opacity: 1;
  stroke-opacity: 1;
}
.lineDotChart__playBtn {
  opacity: .23;
  cursor: pointer;
  transition: opacity .3s;
}
.lineDotChart__playBtn:hover {
  opacity: 1;
}
.lineDotChart__playBtn circle {
  stroke-width: .5;
  stroke: gray;
  fill: white;
}
.lineDotChart__playBtn rect {
  fill: black;
}
.lineDotChart__playBtn path {
  fill: black;
}



