.chicletSlicer {
  font-family: 'Segoe UI', 'wf_segoe-ui_normal', helvetica, arial, sans-serif;
}
.chicletSlicer .clear {
  background-image: url('data:image/png;base64,');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 13px;
  height: 12px;
  margin: 4px 2px;
  float: right;
  cursor: pointer;
}
.chicletSlicer .row {
  white-space: nowrap;
}
.chicletSlicer .hidden {
  display: none !important;
}
.chicletSlicer .slicerBody {
  margin-left: -5px;
}
.chicletSlicer .slicerBody .cell {
  box-sizing: border-box;
  padding: 2px 5px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  width: 100%;
  float: left;
}
.chicletSlicer .slicerBody ul {
  margin: 0;
  padding: 0;
  display: inline;
}
.chicletSlicer .slicerBody li.slicerItemContainer {
  height: 100%;
  width: 100%;
  padding: 2px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: stretch;
  box-sizing: border-box;
}
.chicletSlicer .slicerBody li.slicerItemContainer .slicer-text-wrapper {
  box-sizing: border-box;
  text-align: center;
  order: 0;
  align-self: auto;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-items: stretch;
  overflow: hidden;
  text-overflow: ellipsis;
}
.chicletSlicer .slicerBody li.slicerItemContainer .slicer-img-wrapper {
  max-width: 100%;
  height: auto;
  align-self: center;
}
.chicletSlicer .slicerBody li.slicerItemContainer .stretchImage {
  align-self: auto;
}
.chicletSlicer .slicerBody li.slicerItemContainer .bottomImage {
  order: 1;
}
.chicletSlicer .slicerBody li.slicerItemContainer .slicerItem-disabled {
  opacity: 0.4;
  color: #fff !important;
}
.chicletSlicer .slicerBody.slicerBody-vertical .cell {
  clear: both;
}
.chicletSlicer .slicerBody.slicerBody-vertical .row {
  float: left;
}
.chicletSlicer .slicerItemContainer.selected {
  background: #BDD7EE;
}
.chicletSlicer .row-horizontal {
  display: inline-block;
}
.chicletSlicer .slicerText,
.chicletSlicer .slicerContainer .slicerText {
  padding: 0px 5px;
  line-height: 20px;
}
.chicletSlicer .slicerHeader {
  margin-bottom: 5px;
}
.chicletSlicer .searchHeader {
  overflow: hidden;
}
.chicletSlicer .searchHeader.collapsed {
  display: none;
}
.chicletSlicer .searchHeader.show {
  display: -webkit-box;
  display: flex;
  justify-content: space-between;
  -webkit-align-items: center;
  width: calc(100% - 10px);
  border: 1px solid #212121;
  height: 20px;
}
.chicletSlicer .searchHeader .search {
  cursor: pointer;
  margin: 1px 4px;
  color: #212121;
  width: 17px;
  height: 17px;
  background-size: cover;
  background-image: url('data:image/png;base64,');
}
.chicletSlicer .searchHeader input {
  color: #333333;
  cursor: auto;
  align-self: center;
  flex: 1 1 0px;
  -webkit-flex: 1 1 0px;
  border-style: none;
  min-width: 0px;
  padding: 0;
}
.chicletSlicer .searchHeader input:focus {
  outline-width: 0;
}



