.radarChart .axis path,
.radarChart .axis line {
  fill: none;
  stroke: grey;
  stroke-width: 1px;
}
.radarChart .axis text {
  font-family: sans-serif;
  font-size: 11px;
}
.radarChart .segments path,
.radarChart .segments line {
  fill: none;
  stroke: grey;
  stroke-opacity: 0.75;
  stroke-width: 0.3px;
}
.radarChart .zeroSegment path,
.radarChart .zeroSegment line {
  fill: none;
  stroke: grey;
  stroke-opacity: 0.75;
  stroke-width: 2px;
}
.radarChart .zeroSegment text {
  fill: none;
  font-size: 10px;
  stroke: grey;
  stroke-opacity: 0.75;
  stroke-width: 1px;
}



